let arr = []; //用来存放获取到的所有的标签
let arrFontSize = []; //用来存放获取到的所有的标签原来字体大小

//获取富文本所有子标签
const getAllTags = (obj) => {
  for (let i = 0; i < obj.length; i++) {
    if (obj[i].children) { //当前元素还存在子元素的时候,递归
      getAllTags(obj[i].children);
    }
    //如果有fontSize样式 就保存下来
    if (!!obj[i].style.fontSize) {
      arr.push(obj[i]); //遍历到的元素添加到arr这个数组中间
      arrFontSize.push(parseInt(obj[i].style.fontSize.substring(0, obj[i].style.fontSize.length - 2))); //保存原标签字体大小
    }
  }
}

//设置字体大小  如果你还有其他需求 同样可以设置
const setFontSize = (base) => {
  arr.map((item, index) => {
    if (!!item.style.fontSize) {
      item.style.fontSize = arrFontSize[index] * base + 'em';
      item.style.lineHeight = arrFontSize[index] * base * 1.7 + 'em'; //这里同时 等比例的设置行间距
    }
  });
}

// 将px => rem
export const pxTorem = (content) => {
  content = content.replace(/(\d+)px/g, function(s, t) {
    s = s.replace('px', '');
    var value = parseInt(s) * 1/37.5; // 100px = 1rem
    return value + "rem";
  });
  return content
}

/**
 * fs 字号 enum ['small','normal','heavy','super']
 */
export const setContentFontSize = (fs) => {
  // console.log(store,"store fontsize")
  switch (fs) {
    case "small":
      return "0.8em"
    case "heavy":
      return "1.1em"
    case "super":
      return "1.2em"
    default: //"normal":
      return "1em"
  }
}



