<template>
  <div class="TestInstructions">
    <!-- <NavBar style="position: fixed;top:0;"/> -->
    <section class="container_root">
      <h5>{{content.title}}</h5>
      <div class="content_html" v-html="finalHtml"></div>
    </section>
  </div>
</template>

<script>
import { pxTorem } from "@/utils/fontSize";

import {fetchNotes} from '@/api/exam'
export default {
  name: "TestInstructions",
  props: {},
  data() {
    return {
      content:{
         title: "",
         content: ""
      } 
     
    };
  },

    computed: {
    finalHtml: {
      get() {
        let finalHtml = this.content.content;
        // 测试内链跳转
        // finalHtml =  this.detailHtml +
        //   `<a href="javascript:void(0);"  data_kcz='{"redirect_type": "internal", "redirect_url": "",  "redirectable_id": 201,  "redirectable_type": "article"}'>
        // 测试内链0127(ios)
        // <a>`;

        // 测试iframe
        // finalHtml = this.detailHtml+`<iframe src="https://modao.cc/app/PF07j796UUwlkUWlMuH3shTp3jdeztn/embed" width="515" height="968" allowTransparency="true" frameborder="0"></iframe>`

        //图片兼容手机端
        finalHtml = finalHtml.replace(
          /\<p/gi,
          '<p style="text-indent:2em" '
        );
        //视频兼容手机端
        // finalHtml = finalHtml.replace(
        //   /\<video/gi,
        //   '<video style="max-width:100%;height:auto" controls="controls" '
        // );
        //设置正文字体
        // if (this.detailType == "kcz") {
        //   finalHtml = finalHtml.replace(
        //     /font-size: 18px;/gi,
        //     "font-size: 1em; line-height: 1.75em"
        //   );
        // } else {
        //   finalHtml = finalHtml.replace(
        //     /font-size: 15px;/gi,
        //     "font-size: 1em; line-height: 1.75em"
        //   );
        // }
        finalHtml = pxTorem(finalHtml);
        return finalHtml;
      },
    },
  },

  mounted() {
    this.getNotes();
  },

  methods: {
    // 获取考试须知
    async getNotes() {
      let res = await fetchNotes({});
      let content = res.result[0];
      // content.title = content.content.replace(/\s/g, "");
      this.content = content
    },
  }
};
</script>
<style lang="scss" src="./index.scss" scoped></style>



